<template>
  <div class="edit container">
    <Alert v-if="alert" :message="alert" />

    <form v-on:submit.prevent="updateCustomer">
      <div class="well">
        <div class="form-group">
          <label>姓名</label>
          <input type="text" class="form-control" placeholder="name" v-model="customer.name" />
        </div>
        <div class="form-group">
          <label>电话</label>
          <input type="text" class="form-control" placeholder="phone" v-model="customer.phone" />
        </div>
        <div class="form-group">
          <label>邮箱</label>
          <input type="text" class="form-control" placeholder="email" v-model="customer.email" />
        </div>
        <div class="form-group">
          <label>学历</label>
          <input
            type="text"
            class="form-control"
            placeholder="education"
            v-model="customer.education"
          />
        </div>
        <div class="form-group">
          <label>毕业学校</label>
          <input
            type="text"
            class="form-control"
            placeholder="graduationschool"
            v-model="customer.graduationschool"
          />
        </div>
        <div class="form-group">
          <label>职业</label>
          <input
            type="text"
            class="form-control"
            placeholder="profession"
            v-model="customer.profession"
          />
        </div>
        <div class="form-group">
          <label>个人简介</label>
          <textarea class="form-control" rows="10" v-model="customer.profile"></textarea>
        </div>
        <button type="submit" class="btn btn-primary btn-block">添加</button>
      </div>
    </form>
  </div>
</template>

<script lang="ts">
import { onMounted, ref } from "vue";
import axios from "axios";
import { useRouter, useRoute } from "vue-router";
import Alert from "../components/Alert.vue";

export default {
  conponents: { Alert },
  setup() {
    const router = useRouter();
    const route = useRoute();
    const alert = ref<string>("");

    interface Customer {
      name: string;
      phone: string;
      email: string;
      education: string;
      graduationschool: string;
      profession: string;
      profile: string;
    }
    const customer = ref<Customer>({
      name: "",
      phone: "",
      email: "",
      education: "",
      graduationschool: "",
      profession: "",
      profile: "",
    });

    onMounted(async () => {
      const res = await axios.get(
        "http://localhost:3000/users/" + route.params.id
      );
      customer.value = res.data;
    });

    const updateCustomer = async () => {
      if (
        !customer.value.name ||
        !customer.value.phone ||
        !customer.value.email
      ) {
        alert.value = "请添写对应的信息";
        // console.log("请添加对应的信息");
      } else {
        let updateCustomer = {
          name: customer.value.name,
          phone: customer.value.phone,
          email: customer.value.email,
          education: customer.value.education,
          graduationschool: customer.value.graduationschool,
          profession: customer.value.profession,
          profile: customer.value.profile,
        };

        const res = await axios.put(
          "http://localhost:3000/users/" + route.params.id,
          updateCustomer
        );
        // console.log(res);
        router.push({ path: "/", query: { alert: "用户信息更新成功" } });
      }
    };

    return { customer, updateCustomer, alert };
  },
};
</script>

